/* ProductDetail.css */

.product-detail-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #f4f4f4;
  padding: 2rem;
}

.product-detail-content {
  display: flex;
  background: #ffffff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  width: 100%;
  max-width: 900px;
}

.product-image {
  flex: 1;
  padding: 1rem;
}

.product-image img {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 8px;
}

.product-info {
  flex: 1;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.product-name {
  font-size: 2rem;
  color: #333;
  margin-bottom: 1rem;
}

.product-description {
  font-size: 1rem;
  color: #666;
  margin-bottom: 1rem;
}

.product-price {
  font-size: 1.5rem;
  color: #e63946;
  margin-bottom: 1rem;
}

.add-to-cart-button {
  background-color: #0070ba;
  color: #fff;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.add-to-cart-button:hover {
  background-color: #005bb5;
}

.loading-message {
  font-size: 1.2rem;
  color: #333;
}
