.product-list-container {
  padding: 20px;
  background-color: #f4f4f9;
  text-align: center;
}

.product-list-title {
  font-size: 2.5rem;
  margin-bottom: 30px;
  color: #2c3e50;
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.product-card {
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.product-card:hover {
  transform: scale(1.05);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
}

.product-image {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-bottom: 1px solid #ddd;
}

.product-info {
  padding: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.product-name {
  font-size: 1.5rem;
  margin: 10px 0;
  color: #2c3e50;
}

.product-price {
  font-size: 1.2rem;
  color: #e74c3c;
  margin: 10px 0;
}

.product-description {
  font-size: 1rem;
  color: #7f8c8d;
  margin-bottom: 15px;
}

.product-actions {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.product-button, .view-detail-button {
  background-color: #2980b9;
  color: #ffffff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.product-button:hover, .view-detail-button:hover {
  background-color: #3498db;
}

.view-detail-button {
  background-color: #27ae60;
}

.view-detail-button:hover {
  background-color: #2ecc71;
}
.notification {
  background-color: #28a745;
  color: white;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 20px;
  text-align: center;
}
